import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import Moment from "react-moment";
import { connect } from "react-redux";
import { addLike, removeLike, removePost } from "../../actions/post";
import Spinner from "../layout/Spinner";

const PostItem = ({
  post: { _id, text, name, avatar, user, likes, comments, date },
  auth,
  addLike: addLikeHandler,
  removeLike: removeLikeHandler,
  removePost: removePostHandler,
  showActions,
}) => {
  return auth.user ? (
    <div className="post bg-white p-1 my-1">
      <div>
        <Link to={`/profile/${user}`}>
          <img className="round-img" src={avatar} alt="user avatar" />
          <h4>{name}</h4>
        </Link>
      </div>
      <div>
        <p className="my-1">{text}</p>
        <p className="post-date">
          Posted on: <Moment format="DD/MM/YYYY">{date}</Moment>
        </p>
        {showActions && (
          <>
            <button
              type="button"
              className="btn btn-light"
              onClick={() => addLikeHandler(_id)}
            >
              <i className="fas fa-thumbs-up" />
              <span>{likes.length ? likes.length : ""}</span>
            </button>
            <button
              type="button"
              className="btn btn-dark"
              onClick={() => removeLikeHandler(_id)}
            >
              <i className="fas fa-thumbs-down" />
            </button>
            <Link className="btn btn-primary" to={`/post/${_id}`}>
              Discussion:{" "}
              <span className="comment-count">
                {comments.length ? comments.length : ""}
              </span>
            </Link>
            {auth.user._id === user && (
              <button
                onClick={() => removePostHandler(_id)}
                type="button"
                className="btn btn-danger"
              >
                <i className="fas fa-times" />
              </button>
            )}
          </>
        )}
      </div>
    </div>
  ) : (
    <Spinner size="medium" />
  );
};
PostItem.defaultProps = {
  showActions: true,
};
PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  addLike: PropTypes.func.isRequired,
  removeLike: PropTypes.func.isRequired,
  removePost: PropTypes.func.isRequired,
  showActions: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { addLike, removeLike, removePost })(
  PostItem
);
